.burger__line::before {
  content: '';
  position: absolute;
  top: 7px;
  left: 0;
  width: 100%;
  height: 2px;
  background: black;
  border-radius: 10px;
}

.burger__line::after {
  content: '';
  position: absolute;
  bottom: 7px;
  left: 0;
  width: 100%;
  height: 2px;
  background: black;
  border-radius: 10px;
