  padding: 8px 16px;
  height: 100%;
}

.product .card h3 {
  color: #333333;
  font-size: 16px;
  font-weight: 700;
  line-height: 1;
  margin-bottom: 8px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.product .card-description {
  max-width: 100%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-bottom: 16px;
}

.product .card-description span {
  display: flex;
  align-items: flex-start;
  color: #898989;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.25;
}

.product .card-description span:not(:last-child)::after {
  content: '';
  margin: 0 4px;
}

.product .card .stats-list {
  margin-bottom: 16px;
}

.product .card .stats-item {
  display: flex;
  align-items: flex-start;
  color: #333333;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.2;
}

.product .card .stats-item:not(:last-child) {
  padding-bottom: 4px;
  margin-bottom: 2px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.product .card .stats-item span {
  display: flex;
  align-items: center;
  color: #333333;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
}

.product .card .stats-item span::after {
  content: ':';
  margin: 0 3px;
}

.product .card .price {
  display: block;
  margin-top: auto;
  color: #3374db;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.pagination {
  padding: 32px 0 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination-list {
  display: flex;
  align-items: center;
}

.pagination-item span {
  color: black;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
}

.pagination-item.pages {
  padding: 0 16px;
}

.pagination__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 4px;
  text-decoration: none;
}

.pagination__link i {
  font-size: 16px;
  font-weight: 700;
  color: black;
}

.pagination__link .icon-arrow-double {
  font-size: 20px;
}

.pagination__link.black {
  background: black;
}

.pagination__link.black i {
  color: white;
}

.pagination__link.next i, .pagination__link.last i {
  transform: rotate(180deg);
}

@media screen and (max-width: 1199px) {
  .product.active .product-wrapper {
    flex: 0 0 33.3333%;
    max-width: 33.3333%;
  }
  .product-wrapper {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media screen and (max-width: 991px) {
  .active-list {
    padding-top: 8px;
  }
  .product {
    flex: 0 0 100%;
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
  }
  .product.active .product-wrapper {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .product-wrapper {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media screen and (max-width: 575px) {
  .product.active .product-wrapper {
    flex: 0 0 100%;
    max-width: 100%;
  }
  .product-table {
    margin: 0;
  }
  .product-wrapper {
    flex: 0 0 100%;
    max-width: 100%;
    padding: 8px 0;
  }
  .active-list {
    padding: 8px 16px;
  }
}

.recomendations .container {
  display: block;
}

.recomendations h2 {
  margin-bottom: 24px;
}

.recomendations-table {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: flex-start;
  margin: -8px;
}

.recomendations-wrapper {
  flex: 0 0 auto;
  max-width: fit-content;
  height: fit-content;
  padding: 8px;
}

.recomendations .card {
  display: block;
  padding: 16px 8px;
  border: 1px solid #c7c7c7;
  border-radius: 6px;
  text-decoration: none;
  color: #333333;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
}

.recomendations .card:hover {
  color: white;
  background: #3374db;
  border-color: #3374db;
}

.recomendations .card:focus {
  background: #e9e9e9;
  border-color: #e9e9e9;
}

.recomendations .card:focus:hover {
  color: #333333;
}

@media screen and (max-width: 575px) {
  .recomendations {
    padding: 0 16px;
  }
}

.info {
  padding: 64px 0 24px;
}

.info h2 {
  margin-bottom: 24px;
}

.info p {
  color: black;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
}

@media screen and (max-width: 575px) {
  .info {
    padding: 64px 16px 24px;
  }
}
